<template>
<div style="margin: 0px; height: 100%" id="full">
	<div id="sceneSpace" style="position: absolute; top: 0px; right: 0px; bottom: 0px; left: 0px">
		<div
          style="position: absolute; bottom: 0; right: 0; margin: 0; padding: 0"
        >
          <p class="white--text mr-4" style="margin-top: 10px">
            made by
            <a
              href="https://quadusk.com/"
              target="_bkank"
              style="text-decoration: none"
              ><span class="font-weight-bold font-italic white--text"
                >QUADUSK</span
              ></a
            >
          </p>
        </div>
        <v-row style="width: 100%; position: absolute; top: 95%">
          <v-col
            cols="6"
            md="6"
            class="text-center"
            style="position: absolute; bottom: 0; right: 0"
          >
            <v-row
              style="margin-left: auto; max-width: 200px"
              class="d-flex justify-end align-end"
            >
              <div id="icons">
				<v-btn
                  small
                  icon
                  class="black ml-3"
                  style="animation: mymove 3s" to="/"
                  ><svg style="fill: currentColor; color:white;" height="15px" viewBox="0 1 511 511.999" width="15px" xmlns="http://www.w3.org/2000/svg"><path d="m498.699219 222.695312c-.015625-.011718-.027344-.027343-.039063-.039062l-208.855468-208.847656c-8.902344-8.90625-20.738282-13.808594-33.328126-13.808594-12.589843 0-24.425781 4.902344-33.332031 13.808594l-208.746093 208.742187c-.070313.070313-.144532.144531-.210938.214844-18.28125 18.386719-18.25 48.21875.089844 66.558594 8.378906 8.382812 19.441406 13.234375 31.273437 13.746093.484375.046876.96875.070313 1.457031.070313h8.320313v153.695313c0 30.417968 24.75 55.164062 55.167969 55.164062h81.710937c8.285157 0 15-6.71875 15-15v-120.5c0-13.878906 11.292969-25.167969 25.171875-25.167969h48.195313c13.878906 0 25.167969 11.289063 25.167969 25.167969v120.5c0 8.28125 6.714843 15 15 15h81.710937c30.421875 0 55.167969-24.746094 55.167969-55.164062v-153.695313h7.71875c12.585937 0 24.421875-4.902344 33.332031-13.8125 18.359375-18.367187 18.367187-48.253906.027344-66.632813zm-21.242188 45.421876c-3.238281 3.238281-7.542969 5.023437-12.117187 5.023437h-22.71875c-8.285156 0-15 6.714844-15 15v168.695313c0 13.875-11.289063 25.164062-25.167969 25.164062h-66.710937v-105.5c0-30.417969-24.746094-55.167969-55.167969-55.167969h-48.195313c-30.421875 0-55.171875 24.75-55.171875 55.167969v105.5h-66.710937c-13.875 0-25.167969-11.289062-25.167969-25.164062v-168.695313c0-8.285156-6.714844-15-15-15h-22.328125c-.234375-.015625-.464844-.027344-.703125-.03125-4.46875-.078125-8.660156-1.851563-11.800781-4.996094-6.679688-6.679687-6.679688-17.550781 0-24.234375.003906 0 .003906-.003906.007812-.007812l.011719-.011719 208.847656-208.839844c3.234375-3.238281 7.535157-5.019531 12.113281-5.019531 4.574219 0 8.875 1.78125 12.113282 5.019531l208.800781 208.796875c.03125.03125.066406.0625.097656.09375 6.644531 6.691406 6.632813 17.539063-.03125 24.207032zm0 0"/></svg>
                </v-btn>
                <v-btn
                  small
                  icon
                  class="black ml-3"
                  style="animation: mymove 3s"
                  ><v-icon
                    icon
                    class="white--text ml-n5 mt-2 flaticon-virtual-reality"
                  ></v-icon>
                </v-btn>

                <v-btn
                  small
                  icon
                  class="black ml-3"
                  style="animation: mymove 3s" to="/boreyV360"
                  ><v-icon
                    icon
                    class="white--text ml-n5 flaticon-360-degrees"
                  ></v-icon
                ></v-btn>
                <v-btn
                  small
                  icon
                  class="black ml-3"
                  @click="
                    toggleFullscreen();
                    full_screen = !full_screen;
                  "
                >
                  <v-icon class="white--text" v-show="!full_screen">
                    mdi-arrow-expand-all
                  </v-icon>
                  <v-icon class="white--text" v-show="full_screen">
                    mdi-arrow-collapse-all
                  </v-icon>
                </v-btn>
              </div>
            </v-row>
          </v-col>
        </v-row>
	</div>
	<div v-if="!loaded">
		<LoadingScreen/>
	</div>
</div>
</template>

<script>
import LoadingScreen from '../components/loadingScreen'
import {
	PerspectiveCamera, AmbientLight, Scene, WebGLRenderer, Mesh, AxesHelper,
	sRGBEncoding, PCFSoftShadowMap, CircleBufferGeometry, MeshBasicMaterial,
	Raycaster, Vector2, CylinderBufferGeometry, DoubleSide, AnimationMixer,
	Clock, ShapeBufferGeometry, Group, DirectionalLight, RingBufferGeometry, Vector3,
	FrontSide, HemisphereLight, HemisphereLightHelper, DirectionalLightHelper,
	SpotLight, SpotLightHelper
} from 'three';
const THREE = {
	PerspectiveCamera, AmbientLight, Scene, WebGLRenderer, Mesh, AxesHelper,
	sRGBEncoding, PCFSoftShadowMap, CircleBufferGeometry, MeshBasicMaterial,
	Raycaster, Vector2, CylinderBufferGeometry, DoubleSide, AnimationMixer,
	Clock, ShapeBufferGeometry, Group, DirectionalLight, RingBufferGeometry, Vector3,
	FrontSide, HemisphereLight, HemisphereLightHelper, DirectionalLightHelper,
	SpotLight, SpotLightHelper
}
// import { VertexNormalsHelper } from '~/node_modules/three/examples/jsm/helpers/VertexNormalsHelper'
// import {OrbitControls} from '~/node_modules/three/examples/jsm/controls/OrbitControls.js';
import { LoadGLTFAsync, LoadTextureAsync} from '../script/quadusk/quadusk'
import gsap from 'gsap'


export default {

	data() {
		
		return {
			model: null,
			dialog: false,
			dialog1: false,
			dialog2: false,
			dialog3: false,
			sound: false,
			full_screen: false,
			clicked: false,
			hide: true,
			audio: false,
			fullscreen: false,
			
			loaded: false,
			
			inRoom: false,
			sceneSpace: null,
			camera: null,
			scene: null,
			renderer: null,
			stats: null,

			raycaster: null,
			circleOverHelper: null,
			targetObjects: [],
			moveTargetObjects: [],

			mouse: null,
			mouseDown: null,
			controls: null,
			enableOrbitControls: false,

			intersectPoint: new Vector3(),

			enterRoomTween: null,
			exitRoomTween: null,
			cameraMoveAnimation: null,
			moving: false,
			
			gltfLoader: new LoadGLTFAsync(),
			textureLoader: new LoadTextureAsync()

		}
	},

    components:{
        LoadingScreen
    },

	methods: {

		async init() {

			const loadGLTFPromise = this.gltfLoader.load( [ '/3d_models/Borey_V_3D.gltf' ] )
			// const loadTexturePromise = this.textureLoader.load( [ '/image/360/approaching_storm.jpg' ] )

			// Container
			this.sceneSpace = document.getElementById('sceneSpace');
			// this.sceneSpace.name = "Scene Space";
			
			// WebGL Renderer
			{
				this.renderer = new WebGLRenderer({antialias: true});
				// this.renderer.name = "Renderer";
				// this.renderer.setClearColor(0x888888);
				this.renderer.setClearColor(0x7198c7 );

				this.renderer.setPixelRatio(window.devicePixelRatio);
				this.renderer.setSize(this.sceneSpace.clientWidth, this.sceneSpace.clientHeight);
				
				// this.renderer.outputEncoding = sRGBEncoding;
				// this.renderer.shadowMap.enabled = true;
				// this.renderer.shadowMap.type = PCFSoftShadowMap;
				this.sceneSpace.appendChild(this.renderer.domElement);
			}

			// Scene
			{
				this.scene = new Scene();
				this.scene.name  = "Scene";
			}
			
			// Camera
			{
				const fov = 75;
				const aspect = this.sceneSpace.clientWidth / this.sceneSpace.clientHeight;
				const near = 0.1;
				const far = 5000;
				this.camera = new PerspectiveCamera(fov, aspect, near, far);
				this.camera.name = "Camera";
				// this.camera.rotateY = Math.PI * 5;
				this.camera.position.set(0, 30, 110);
			}
			
			// Light
			{
				// const color = 0xffffff;
				// const intensity = 0.8;
				// const ambientLight = new AmbientLight(color, intensity);
				// ambientLight.name = "Ambient Light";
				// this.scene.add(ambientLight);
			}
			
			// Helper
			// {
			// 	let helper = new AxesHelper(300);
			// 	this.scene.add(helper);
			// }
			
			// Circle Helper
			{

				const geometry = new RingBufferGeometry( 0.5, 1, 32 );
				const material = new MeshBasicMaterial( { color: '#1ed4b2', side: FrontSide } );
				this.circleOverHelper = new Mesh( geometry, material );
				this.circleOverHelper.rotateX(-Math.PI/2);
				// this.circleOverHelper.geometry.computeVertexNormals()

				this.scene.add(this.circleOverHelper);
			}
			
			// Orbit Controls
			// {
			// 	this.controls = new OrbitControls(this.camera, this.renderer.domElement);
			// 	this.controls.target.set(0,0,0);
			// 	// this.controls.minDistance = 10;
			// 	// this.controls.maxDistance = 100;
			// 	this.controls.update();
			// 	this.controls.enabled = false
			// }

			// Hemisphere light
			{
				const light = new THREE.HemisphereLight( 0xb8e5f2, 0x080820, 1.5 ); 
				// const light = new THREE.HemisphereLight( 0x87cefa, 0x808080, 1 ); 
				this.scene.add( light );
				// const helper = new THREE.HemisphereLightHelper(light, 150, 0x9a89af)
				// this.scene.add(helper)
			}

			// Directinal light
			{
				// White directional light at half intensity shining from the top.
				const directionalLight = new THREE.DirectionalLight( 0x808080, 0.8 );
				directionalLight.position.set( 0, 50, 50 )
				this.scene.add( directionalLight );
				// directionalLight.target = this.scene.children;
				// this.scene.add(directionalLight.target)
				// const helper = new THREE.DirectionalLightHelper( directionalLight, 20, 0x98af7ab )
				// this.scene.add( helper )
			}

			{
				// White directional light at half intensity shining from the top.
				const directionalLight = new THREE.DirectionalLight( 0x808080, 0.8 );
				directionalLight.position.set( -10, 20, -80 )
				this.scene.add( directionalLight );
				// directionalLight.target = this.scene.children;
				// this.scene.add(directionalLight.target)
				// const helper = new THREE.DirectionalLightHelper( directionalLight, 20, 0x98af7ab )
				// this.scene.add( helper )
			}
			// {
			// 	// White directional light at half intensity shining from the top.
			// 	const directionalLight = new THREE.DirectionalLight( 0x808080, 0.6 );
			// 	directionalLight.position.set( 0, -10, 100 )
			// 	this.scene.add( directionalLight );
			// 	// directionalLight.target = this.scene.children;
			// 	// this.scene.add(directionalLight.target)
				// const helper = new THREE.DirectionalLightHelper( directionalLight, 20, 0x98af7ab )
				// this.scene.add( helper )
			// }

			// Raycaster
			this.raycaster = new Raycaster();
			
			// Mouse
			this.mouse = new Vector2();

			//#region load gltf
			await loadGLTFPromise
				.then( gltfs => {

					// console.log(gltfs[0]);
					gltfs[0].scene.scale.set( 0.5, 0.5, 0.5)
					// const semen = gltfs[0].scene.children.getObjectByName('M_0049_Beige')
					// gltfs[0].scene.children[0].children[1].material.color.set(0x2b2e3c)
					gltfs[0].scene.children[0].children[1].material.color.set(0x242b36)

					const glass = gltfs[0].scene.children[0].children[4]
					glass.material.color.set(0x23acc4)
					glass.material.transparent = true
					glass.material.opacity = 0.2
					glass.material.roughness = 0.1
					glass.material.metalness = 0.6

					// const ground = gltfs[0].scene.children[0].children[0]
					// ground.geometry.computeVertexNormals()
					// console.log(  );
					// const helper = new VertexNormalsHelper( ground, 20, 0x00ff00, 1 );
					// this.scene.add(helper)

					this.targetObjects.push( gltfs[0].scene.children[0].children[0] )
					this.targetObjects.push( gltfs[0].scene.children[0].children[1] )
					this.targetObjects.push( gltfs[0].scene.children[0].children[8] )
					this.targetObjects.push( gltfs[0].scene.children[0].children[9] )

					this.moveTargetObjects.push( gltfs[0].scene.children[0].children[9] )

					this.scene.add(gltfs[0].scene)
					this.loaded = true

					gsap.to(this.camera.position, {

						// x: this.intersectPoint.x,
						y: 3,
						z: 72,

						ease: "Power3.InOut",
						duration: 2,
						// paused: true,
						// reversed: true,
						delay: 0.5,

						onStart: () => {
							this.moving = true;
							// console.log('haha')
							// console.log('arrived');

						},

						onComplete: () => {
							this.moving = false;
						}

					});

				} )
			//#endregion

			//#region texture for scene backgrouong
			// await loadTexturePromise
			// .then( textures => {

			// 	this.scene.background = textures[0]

			// } )
			//#endregion

			// Animation
			this.renderer.setAnimationLoop(this.animate);

		},

		getFullscreenElement() {
			return (
				document.fullscreenElement || //standard property
				document.webkitFullscreenElement || //safari/opera support
				document.mozFullscreenElement || //firefox support
				document.msFullscreenElement
			); //ie/edge support
		},

		toggleFullscreen() {
		if (this.getFullscreenElement()) {
			document.exitFullscreen();
		} else {
			document.documentElement.requestFullscreen().catch(console.log);
		}
		},

		switchControl() {

			this.enableOrbitControls = true
			this.controls.update();

		},
		
		animate() {

			this.renderer.render(this.scene, this.camera);
			
		},


		initControls() {

			this.camera.rotation.order = "YXZ";

			// Mouse down
			this.sceneSpace.addEventListener("pointerdown", () => {
				this.mouseDown = true;
				this.sceneSpace.style.cursor = "grab";
			}, false);

			// Mouse up
			this.sceneSpace.addEventListener("pointerup", () => {
				this.sceneSpace.style.cursor = "default";
				this.mouseDown = false;
			}, false);

			this.sceneSpace.addEventListener("pointermove", (evt) => {
				let movementX = evt.movementX || evt.mozMovementX || evt.webkitMovementX || 0;
				let movementY = evt.movementY || evt.mozMovementY || evt.webkitMovementY || 0;
				if (this.mouseDown) {
					this.sceneSpace.style.cursor = "grabbing";
					this.camera.rotation.y -= -movementX / 600;
					this.camera.rotation.x -= -movementY / 600;
				}
			},false);

		},

		onMouseMove(event) {

			event.preventDefault();

			if(!this.moving){

				this.mouse.set(
					(event.clientX / this.renderer.domElement.clientWidth) * 2 - 1,
					-(event.clientY / this.renderer.domElement.clientHeight) * 2 + 1
				);

				this.raycaster.setFromCamera(this.mouse, this.camera);
				let intersects = this.raycaster.intersectObjects(this.targetObjects);

				if (intersects.length) {

					let intersect = intersects[0];

					if ( intersect.point.distanceTo( this.camera.position ) > 14 ) {
						return 
					}else {
						
						this.circleOverHelper.position.copy(intersect.point).add(intersect.face.normal);
						this.circleOverHelper.position.y += 0.4;

					}

				}

			}
		},

		onMouseDown( event ) {

			// event.preventDefault();

			this.mouse.set(

				(event.clientX / this.renderer.domElement.clientWidth) * 2 - 1,
				-(event.clientY / this.renderer.domElement.clientHeight) * 2 + 1

			);

			this.raycaster.setFromCamera(this.mouse, this.camera);
			let intersects = this.raycaster.intersectObjects(this.targetObjects);

			if (intersects.length) {
				// this.inRoom = true;

				// console.log('arrived');

				let intersect = intersects[0];

				this.intersectPoint.x = intersect.point.x;
				this.intersectPoint.y = intersect.point.y;
				this.intersectPoint.z = intersect.point.z;

				// console.log(this.intersectPoint);

				if ( intersect.object.name === 'Exterior_-V_(2)_1' || intersect.point.distanceTo( this.camera.position ) > 17 ){

					return

				}else{

					// console.log( intersect.object.name );

					gsap.to(this.camera.position, {
	
						x: this.intersectPoint.x,
						y: this.intersectPoint.y + 4,
						z: this.intersectPoint.z,

						ease: "Power3.InOut",
						duration: 2,
						// paused: true,
						// reversed: true,

						onStart: () => {
							this.moving = true;
							// console.log('haha')
							console.log('arrived');
	
						},
	
						onComplete: () => {
							this.moving = false;
						}
	
						});
				}


			}

		},
		
		onWindowResize() {

			this.renderer.setSize(this.sceneSpace.clientWidth, this.sceneSpace.clientHeight);

			this.camera.aspect = this.sceneSpace.clientWidth / this.sceneSpace.clientHeight;
			this.camera.updateProjectionMatrix();

		},
	},

	mounted() {

		this.init();
		this.initControls();
		this.sceneSpace.addEventListener("mousemove", this.onMouseMove, false);
		this.sceneSpace.addEventListener("mousedown", this.onMouseDown, false);

		window.addEventListener('resize', this.onWindowResize, false);

	},

	created(){
	}
}

</script>
